body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: rgba(0, 0, 0);
}

.progress-bar-container{
    z-index: 20;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 66.66%; /* 2/3 of the window width */
}
.info{
    z-index: 10;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.info p {
    color: azure;
    font-size: large;
    width: 66.66%; /* 2/3 of the window width */
    transition: color 0.3s; /* Add a smooth color transition effect */
}

p:hover {
    color: purple; /* Change the color to purple on hover */
}

p:active {
    color: purple; /* Change the color to purple when the element is clicked */
}
#progress-bar {
    width: 30%;
    margin-top: 0.5%;
    height: 2%;
}
.btn-info{
    z-index: 5 !important;
}
label {
    color: white;
    font-size: 2rem;
}